/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* borders vallen hiermee binnen afmetingen van een element:
*/

*,
*::after,
*::before {
  box-sizing: border-box;
}

.bb-block {
  padding: 8px 12px;
  margin: 10px 0;
  background-color: #f5f5f5;
  box-shadow: 0 0 10px #ccc;
  transition: box-shadow .4s ease-out;
}

.bb-block:hover {
  box-shadow: 0 0 10px #999;
}

.bb-block h3 {
  margin: 0.5em 0 1em 0;
}

.bb-block hr {
  background-color: #ccc;
  border-bottom: 1px solid #fff;
}

/* ketzerception */

.bb-block .bb-block {
  padding: 0;
  margin: 0;
  box-shadow: none;
}

.bb-block .bb-block .groep-tabs {
  visibility: hidden;
}

.bbcodePreview {
  display: none;
  margin: 7px 0;
  padding: 2px;
  background-color: #ffffc1;
  border: 1px solid #ccc;
}

.bbcodePreview::after {
  content: "";
  display: block;
  clear: both;
}

.bbcodePreview,
.bbcodePreview div,
.Formulier .bbcodePreview {
  line-height: inherit;
}

.offtopic {
  font-size: .9em;
  line-height: 1em;
  color: #bbb;
}

.offtopic a {
  color: #bbb;
}

.offtopic hr {
  text-align: left;
  background-color: #bbb;
}

.offtopic img {
  opacity: 0.3;
  filter: alpha(opacity=30);
}

.bb-prive {
  background-color: #FFF0F5;
}

.bb-maaltijd {
