  background-repeat: repeat-x;
  color: white;
  font-weight: bold;
}

a.maaltijd-aangemeld,
span.maaltijd-aangemeld {
  font-weight: bold;
  color: green;
  background-color: #f5f5f5;
  border: 1px solid #a9a9a9;
  text-shadow: none;
  background-image: none;
}

a.maaltijd-afgemeld,
span.maaltijd-afgemeld {
